import Endpoint from '@/services/Endpoint'

export default {
  communicationEvents (payload) {
    return Endpoint().get('/s1/nucleus/communicationEvent', {
      params: payload
    })
  },
  createCommunicationEvent (payload) {
    return Endpoint().post('/s1/nucleus/communicationEvent', payload)
  },
  modifyCommunicationEvent (payload) {
    return Endpoint().patch('/s1/nucleus/communicationEvent/', payload)
  },
  communicationEventTypes (payload) {
    return Endpoint().get('/s1/nucleus/communicationEvent/type', {
      params: payload
    })
  },
  communicationEventContents (payload) {
    return Endpoint().get(`/s1/nucleus/communicationEvent/${payload.communicationEventId}/content`, {
      params: payload
    })
  },
  createCommunicationEventContent (payload) {
    return Endpoint().post(`/s1/nucleus/communicationEvent/${payload.communicationEventId}/content`, payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  }
}
