import CommunicationEventService from '@/services/nucleus/CommunicationEventService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CommunicationEventTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultCommunicationEventTableFilter: {
      class: String,
      default: null
    },
    defaultCommunicationEventTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      communicationEventTableData: [],
      communicationEventTableFilterTimer: null,
      communicationEventTableFilter: this.defaultCommunicationEventTableFilter,
      communicationEventTableOrderBy: this.defaultCommunicationEventTableOrderBy,
      communicationEventTableContainer: 'loading-container',
      communicationEventTableBusy: false,
      communicationEventTableTotalRows: 0,
      communicationEventTableCurrentPage: 1,
      communicationEventTablePerPage: 10
    }
  },
  computed: {
    communicationEventTableNoDataText: function () {
      return this.communicationEventTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    communicationEventTableTotalPages: function () {
      return Math.ceil(this.communicationEventTableTotalRows / this.communicationEventTablePerPage)
    },
    communicationEventTablePagingInfo: function () {
      return this.pagingInfo(this.communicationEventTableCurrentPage, this.communicationEventTablePerPage, this.communicationEventTableTotalRows)
    }
  },
  methods: {
    loadCommunicationEventTableData (loadingContainerId) {
      let self = this
      this.communicationEventTableBusy = true
      this.communicationEventTableContainer = loadingContainerId || this.communicationEventTableContainer
      this.startLoading(this.communicationEventTableContainer)
      CommunicationEventService.communicationEvents({
        pageIndex: this.communicationEventTableCurrentPage - 1,
        pageSize: this.communicationEventTablePerPage,
        filter: this.communicationEventTableFilter,
        orderByField: this.communicationEventTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.communicationEventTableContainer)
        self.communicationEventTableBusy = false
        self.communicationEventTableTotalRows = response.data.totalRows
        self.communicationEventTableData = response.data.eventList || []
      }).catch(function (error) {
        self.stopLoading(self.communicationEventTableContainer)
        self.communicationEventTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCommunicationEventTable () {
      this.loadCommunicationEventTableData()
    },
    filterCommunicationEventTable (filter) {
      let self = this
      if (this.communicationEventTableFilter !== filter) {
        clearTimeout(this.communicationEventTableFilterTimer)
        this.communicationEventTableFilterTimer = setTimeout(() => {
          self.communicationEventTableFilter = filter
          self.loadCommunicationEventTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.communicationEventTableFilterTimer)
      }
    },
    orderCommunicationEventTable (orderBy, active) {
      this.communicationEventTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCommunicationEventTableData()
    }
  },
  watch: {
    communicationEventTableCurrentPage: function () {
      this.refreshCommunicationEventTable()
    },
    communicationEventTablePerPage: function () {
      this.refreshCommunicationEventTable()
    }
  }
}

export default CommunicationEventTableMixin
